$mautim: #87509c; //Tím
$maungoc: #17c2a4; // Cyan
$mauvang: #ffdd99; // Vang
$maudo: #d74680; // Đỏ
$mauxanh: #3c5499; // Xanh
// phan silder trang chu
.home_slider {
    background: $mautim;
    min-height: 368px;
    color: #ffffff;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    .btn-more {
        background: $maungoc;
        color: #333333;
        padding: .5rem 3rem; } }
// phan sevices trang chu
.home_sevices {
    background: $maungoc;
    min-height: 679px;
    padding: .5rem .7rem;
    color: #ffffff;
    text-align: center;
    justify-content: center;
    align-items: center;
    .md.mid-center {

        text-align: center;
        align-items: center;
        h2 {
            font-size: 2.5rem;
            text-transform: uppercase;
            letter-spacing: .3rem;
            font-weight: bold; }
        p {}
        font-size: 1rem; }
    .col-sm-3 {
        padding: 1rem .8rem;
        .figure-caption {
            text-transform: uppercase;
            font-size: 1.5rem;
            color: #ffffff;
            font-weight: bold;
            padding: 1rem; } } }
